td{
    text-align: center;
    color: #000;
}

th{
    width: 185px;
    text-align: center;
}

tr:hover{
    background-color: #ffa;
}

td,th{
    position: relative;
}

.table td,
.table th{
    padding: 0.75rem;
    vertical-align: top;
    border-top: none;
}

td:hover::after,
th:hover:after{
    content: "";
    position: absolute;
    background-color: #f2f2f2;
    left: 0;
    top: -5000px;
    height: 5000px;
    width: 100%;
    z-index: -1;
}

 .table tr th:hover:nth-child(5){
    background-color: rgb(231,83,115);
    color: aliceblue;
    border: 0.5px solid rgb(12,12,12);
}
 .table tr td:hover:nth-child(5){
    background-color: rgb(231,83,115);
    color: aliceblue;
    border: 0.5px solid rgb(12,12,12);
}

.table tr td:hover{
    background-color: rgb(117, 223, 112);
    color: aliceblue;
    border: 0.5px solid rgb(12, 12, 12);
} 

.thead-dark{
    font-weight: bold;
    text-transform: uppercase;
}