td{
    text-align: center;
    color: #000;
}

th{
    width: 185px;
    text-align: center;
}

tr:hover{
    background-color: #ffa;
}

td,th{
    position: relative;
}

.table td,
.table th{
    padding: 0.75rem;
    vertical-align: top;
    border-top: none;
}

td:hover::after,
th:hover:after{
    content: "";
    position: absolute;
    background-color: #f2f2f2;
    left: 0;
    top: -5000px;
    height: 5000px;
    width: 100%;
    z-index: -1;
}

 .table tr th:hover:nth-child(5){
    background-color: rgb(231,83,115);
    color: aliceblue;
    border: 0.5px solid rgb(12,12,12);
}
 .table tr td:hover:nth-child(5){
    background-color: rgb(231,83,115);
    color: aliceblue;
    border: 0.5px solid rgb(12,12,12);
}

.table tr td:hover{
    background-color: rgb(117, 223, 112);
    color: aliceblue;
    border: 0.5px solid rgb(12, 12, 12);
} 

.thead-dark{
    font-weight: bold;
    text-transform: uppercase;
}
.donation-body{
    background-color: rgb(34, 33, 33);
    color: #fff;
}

.help{
    color: rgb(214, 33, 33);
}

.para{
    color: goldenrod;
    font-weight: 400;
    font-family: 'Oswald', sans-serif;
}

.donation-body ul li a {
    text-decoration: none;
    color: hotpink;
}
.hero{
    color: crimson;
    -webkit-text-decoration-line: underline;
            text-decoration-line: underline;
    -webkit-text-decoration-color: black;
            text-decoration-color: black;
    -webkit-text-decoration-style: solid;
            text-decoration-style: solid;
    text-underline-offset: 10px;
}

.box{
    border: 1px solid black;
    position: relative;
    width: 300px;
    overflow: hidden;
    cursor: pointer;
}

.box img{
    height: 300px;
    /* width: 300px; */
    width: 100%;    
}

.box .content{
    color: #fff;
    background-color: rgba(0,0,0,0.5);
    position: absolute;
    top: 100%;
    left: 0;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    text-align: center;
    transition: all 0.3s;
}

.box:hover .content{
    cursor: pointer;
    top: 85%;
}

@media (max-width: 992px){
    .center{
        display: block;
        margin-left: auto;
        margin-right: auto;
    }
    
}

